import DeepData from '../../../assets/js/formate.js'
export default {
  name:'bjl_room_config',
  data() {
    return {
      id:0,
      keys:'',
      activeName: "",
      ruleForm: {
        cost: "",
        max: "",
        min: "",
        sit_coins_limit: "",
        min_bet: "",
        person_limit: "",
        times_5_limit: "",
        times_10_limit: "",
        times_20_limit: "",
        times_30_limit: "",
        times_40_limit: "",
        all_times_5_limit: "",
        all_times_10_limit: "",
        all_times_20_limit: "",
        all_times_30_limit: "",
        all_times_40_limit: "",
        type_id: "",
        name: "",
        open_game: "",
        open_robot: "",
        robot_type: "",
        is_hundred_game: "",
      },
      rules: {
         name: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         open_game: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         open_robot: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         cost: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         max: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         min: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         sit_coins_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         min_bet: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         person_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         times_5_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         times_10_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         times_20_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         times_30_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         times_40_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         all_times_5_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         all_times_10_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         all_times_20_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         all_times_30_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
         all_times_40_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
      },
         //房间配置的所有数据
      allData:{},
      //当前游戏房间的所有数据
      currentlist:{},
      //匹配当前游戏的条件
      namelist:['200600','200601','200602','200603','200604','200605'],
      //游戏场次类别
      labellist:[],
      loading: false,
    };
  },
   async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.Getroomdata2006({
      key: "roomdata.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.allData = res;
    this.namelist.forEach((item,index)=>{
      Object.keys(res).forEach((it)=>{
        if(item === it){
          this.currentlist[item] = res[it]
          this.currentlist[item].person_limit = res[it].person_limit.replace(/\{|}/g,'')
          this.labellist.push(res[it].name)
        }
        if(index === 0){
          this.activeName = item
          this.ruleForm = res[item]
        }
      })
    });
    // console.log(this.currentlist,this.allData,this.ruleForm);
    
  },
  methods: {
    handleClick(tab) {
      this.namelist.forEach((item)=>{
        if(item === tab.name){
          this.ruleForm = this.currentlist[item]
        }
      })
    },
    submitForm(formName,type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          let resData = DeepData(this.allData);
          this.namelist.forEach((item)=>{
            Object.keys(resData).forEach((it)=>{
              if(item === it){
                resData[it].person_limit = `{${resData[item].person_limit}}`
              }
            })
          })
          //  console.log(this.allData,this.currentlist,this.ruleForm,resData);
          
          if(type === 1){
            //put
                 let { data } = await this.$http.HallFunConfig.Putroomdata2006({
              keys: this.keys,
              values: JSON.stringify(resData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }else{
            this.$message({
              type: "warning",
              message: "保存失败!"
            });
          }
          }else if(type === 2){
            //post
             this.loading = true;

            let { data } = await this.$http.HallFunConfig.Postroomdata2006({
              keys: this.keys,
              values: JSON.stringify(resData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            }else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败!"
              });
            }
          }
        }else{
           console.log("error submit!!");
          return false;
        }
      })
    },
  }
};



